import React, {useEffect, useState} from "react"
import {useDispatch, useSelector} from "react-redux"
import {selectDevice, setDeviceAsync} from "../features/device/deviceSlice"
import {Button, Card, Col, Container, Form, Row} from "react-bootstrap"

function Device() {
  const device = useSelector(selectDevice)
  const dispatch = useDispatch()
  const [currentDevice, setDevice] = useState()

  useEffect(() => {
    setDevice(device)
  }, [device])

  function handleChange(e) {
    setDevice({
      id: e.target.value
    })
  }

  function handleSubmit(e){
    e.preventDefault()
    console.log(e)
    dispatch(setDeviceAsync(e.target.device.value))
  }

  return (
    <Container fluid>
      <Row>
        <Col md="8">
          <Card>
            <Card.Header>
              <Card.Title as="h4">Choose device</Card.Title>
            </Card.Header>
            <Card.Body>
              <label>Device ID</label>
              <Form onSubmit={e => handleSubmit(e)}>
                <Form.Control
                  value={currentDevice ? currentDevice.id : "0"}
                  type="text"
                  name={"device"}
                  onChange={e => handleChange(e)}
                />
                <Button
                  className="btn-fill pull-right"
                  type="submit"
                  variant="info"
                >
                  Set Device
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  )
}

export default Device
